import React from "react";
import './navbar.css';
import { Link } from 'react-router-dom';
export default class Navbar extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      displayList : false
    }
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick = ()=>{
      this.props.handleChange();
      this.setState(state=>({
        displayList : !state.displayList
      }))
  }
  scrollToTop=()=>{
    window.scrollTo(0,0)
  }
  render(){
    return(
    <header className={'fixed-top ' + (this.props.isMobile?" dropNav":"")}
    >   

        <div>

          <a href="/" className="text-decoration-none">
            <svg className="bi me-2" width="40" height="32"></svg>
            <span className={"heading"}>The SafeCure</span>
          </a>
          {this.props.isMobile && <button onClick={this.handleClick}>
          <svg version="1.0" xmlns="http://www.w3.org/2000/svg"
                width="30px" height="30px" viewBox="0 0 128.000000 128.000000"
                preserveAspectRatio="xMidYMid meet">
                <metadata>
                Created by potrace 1.16, written by Peter Selinger 2001-2019
                </metadata>
                <g transform="translate(0.000000,128.000000) scale(0.100000,-0.100000)"
                fill="#000000" stroke="none">
                <path d="M115 1251 c-48 -22 -69 -44 -90 -94 -13 -31 -15 -107 -15 -519 0
                -470 0 -483 21 -526 13 -26 38 -54 62 -70 l41 -27 500 0 501 0 41 24 c24 13
                52 41 65 65 l24 41 0 501 0 500 -27 41 c-16 24 -44 49 -70 62 -43 21 -55 21
                -529 21 -448 0 -487 -2 -524 -19z m949 -61 c51 -11 86 -38 113 -86 22 -39 23
                -43 23 -464 0 -421 -1 -425 -23 -464 -27 -48 -62 -75 -113 -86 -51 -12 -797
                -12 -848 0 -48 11 -84 36 -113 80 -23 33 -23 33 -23 470 0 437 0 437 22 470
                29 43 67 70 109 80 47 11 805 12 853 0z"/>
                <path d="M142 1058 c-15 -15 -16 -60 0 -76 9 -9 133 -12 498 -12 477 0 487 0
                505 20 15 16 17 26 9 48 l-9 27 -496 3 c-376 2 -498 -1 -507 -10z"/>
                <path d="M142 688 c-15 -15 -16 -60 0 -76 9 -9 133 -12 498 -12 477 0 487 0
                505 20 15 16 17 26 9 48 l-9 27 -496 3 c-376 2 -498 -1 -507 -10z"/>
                <path d="M142 328 c-15 -15 -16 -60 0 -76 9 -9 133 -12 498 -12 477 0 487 0
                505 20 15 16 17 26 9 48 l-9 27 -496 3 c-376 2 -498 -1 -507 -10z"/>
              </g>
          </svg>
            </button>}
        </div>
        <ul className={!this.props.isMobile ?" desktopList ": " mobileList" + (!this.state.displayList?" hideThis":"")} >
            <li ><Link to="/"  aria-current="page" onClick={this.scrollToTop}>Home</Link></li>
            <li ><Link to="/services" onClick={this.scrollToTop}>Services</Link></li>
            <li ><Link to="/contact" onClick={this.scrollToTop}>Contact Us</Link></li>
            <li ><Link to="/about" onClick={this.scrollToTop}>About</Link></li>
            <li ><button className="btn btn-primary"><a href="tel:8192061175" style={{color:"white",textDecoration:"none"}} >Call Us : 8192061175 </a></button></li>
            
        </ul>
        
    </header>
    )
  }
}