import React from 'react';
var name,mob_no,service='Nurse'
export default class Form extends React.Component{
    constructor(props){
        super(props);
        this.state={
            name:"",
            mob_no:"",
            service:"Nurse"
        }
        
    }
    handleNameChange = (event)=>{
        name = event.target.value
        this.setState(state=>({
            name:name,
            mob_no:state.mob_no,
            service:state.service

        }))
    }
    handleMobChange = (event)=>{
        mob_no = event.target.value
        
        this.setState(state=>({
            name:state.name,
            mob_no:mob_no,
            service:state.service

        }))
    }
    handleServiceChange = (event)=>{
        service = event.target.value
        this.setState(state=>({
            name:state.name,
            mob_no:state.mob_no,
            service:service

        }))
        
    }
    handleSubmit = (event)=>{
        event.preventDefault()
        const url = 'https://0ikvfw3bp8.execute-api.ap-south-1.amazonaws.com/dev'
        
        const state = this.state
        const requestOptions = {
        method: 'POST',
        
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({name:state.name,mob_no:state.mob_no,service:state.service,type:'request'})
        };
        fetch(url, requestOptions)
        .then(response => alert("Thank You! We have recieved your request. We will be contacting you soon"))
        .catch(error => alert('OOops! The server Encounterd and error. You can call us for appointment'))
        this.setState({
            name:"",
            mob_no:"",
            service:"Nurse"
        })
    }
        
    
    render(){
        var name,mob_no,service
        return(
            <div >
	<iframe name = "dump"></iframe>
                <ul className="list-group list-group-flush" style={{borderRadius:"100px"}}>
                    <li className="list-group-item">
                    <p >Submit your requirement with us and speak to one of our expert healthcare consultants.</p>
                
                    </li>
			
                    <li className="list-group-item">
                        <form onSubmit={this.handleSubmit} target="dump">
                            <div className="mb-3">
                                <label className="form-label">Name</label>
                                <input className="form-control" name="name"  aria-describedby="nameHelp" required={true} onChange={this.handleNameChange} value = {this.state.name}/>
                                </div>
                            <div className="mb-3">
                                <label className="form-label">Phone Number</label>
                                <input  className="form-control" name="mob_no" required={true} onChange={this.handleMobChange} value={this.state.mob_no}/>
                                <div className="form-text">We'll never share your phone number with anyone else.</div>
                            </div>
                            <div className="mb-3 form-check">
                                <input type="radio" name="service"onChange={this.handleServiceChange} value="Attendent" className="form-check-input" checked={this.state.service=="Attendent"} />
                                <label className="form-check-label">Attendent</label>
                            </div>

                            <div className="mb-3 form-check">
                                <input checked={this.state.service=='Nurse'} onChange={this.handleServiceChange} type="radio" name="service" value="Nurse" className="form-check-input"  />
                                <label className="form-check-label" >Nurse</label>
                            </div>


                            <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                
                </li>
                </ul>
            </div>
        )
    }
}