import React from 'react' ; 
class Contact extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            name:'',
            mob_no:'',
            mail:'',
            message:'',
            service:'not applicable'
        }
    }
    
    
    
    
    handleNameChange = (event)=>{
        const name = event.target.value
        this.setState(state=>({
            name:name,
            mob_no:state.mob_no,
            service:state.service,
            mail:state.mail,
            message:state.message

        }))
    }
    handleMobChange = (event)=>{
        const mob_no = event.target.value
        
        this.setState(state=>({
            name:state.name,
            mob_no:mob_no,
            service:state.service,
            mail:state.mail,
            message:state.message

        }))
    }
    handleMailChange = (event)=>{
        const mail = event.target.value
        var state = this.state
        this.setState({
            name:state.name,
            mob_no:state.mob_no,
            service:state.service,
            mail:mail,
            message:state.message
        })
    }
    handleMessageChange = (event)=>{
        const message = event.target.value
        
        this.setState(state=>({
            name:state.name,
            mob_no:state.mob_no,
            service:state.service,
            mail:state.mail,
            message:message
        }))
    }
    
    handleSubmit = (event)=>{
        event.preventDefault()
        const url = 'https://0ikvfw3bp8.execute-api.ap-south-1.amazonaws.com/dev'
        
        const state = this.state
        const requestOptions = {
        method: 'POST',
        
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({name:state.name,mob_no:state.mob_no,service:state.service,type:'enquiry',mail:state.mail,message:state.message})
        };
        fetch(url, requestOptions)
        .then(response => alert("Thank You! We have recieved your message. We will be contacting you soon"))
        .catch(error => alert('OOops! The server Encounterd and error. You can call us for appointment'))
        this.setState({
            name:"",
            mob_no:"",
            service:"Not Applicable",
            message:"",
            mail:""
        })
    }
      
  render() {  
    return (
      <div>
        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3548.9718800095075!2d78.00124551505084!3d27.18862058300962!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3974773b98684b0b%3A0x271181ebb5984807!2sLady%20Lyall%20Hospital!5e0!3m2!1sen!2sin!4v1625131819015!5m2!1sen!2sin" width={this.props.width} height="450" style={{border:"0"}} allowfullscreen="" loading="lazy"></iframe>
      <div className="row">
      <div className="col-lg-6">
          <div className="card shadow mb-3 mb-lg-0" style={{marginTop: "-200px"}}>
              <div className="card-body p-3 p-md-4 p-lg-5">
                  <h3 className="card-title text-primary">Enquiry</h3>
                  <p className="card-text mb-3 mb-md-4 mb-lg-5">Submit an enquiry with us and one of our executives shall get back to you shortly.</p>
                  <form onSubmit={this.handleSubmit}>
                      <div className="row">
                          <div className="col-sm-6">
                              <div className="form-group">
                                  <label for="enquiry-name">Name <span className="text-danger">*</span></label>
                                  <input onChange={this.handleNameChange} value = {this.state.name}className="form-control form-control-lg " id="enquiry-name" name="name" required="" />
                                                                          </div>
                          </div>
                          <div className="col-sm-6">
                              <div className="form-group">
                                  <label for="enquiry-phone">Phone number <span className="text-danger">*</span></label>
                                  <input onChange={this.handleMobChange} value = {this.state.mob_no} className="form-control form-control-lg " id="enquiry-phone" name="phone" required="" type="tel" />
                                                                          </div>
                          </div>
                      </div>
                      <div className="form-group">
                          <label for="enquiry-email">Email address</label>
                          <input onChange={this.handleMailChange} value = {this.state.mail} className="form-control form-control-lg " id="enquiry-email" name="email" type="email" />
                                                          </div>
                      <div className="form-group">
                          <label for="enquiry-message">Message <span className="text-danger">*</span></label>
                          <textarea onChange={this.handleMessageChange} value = {this.state.message}className="form-control form-control-lg " id="enquiry-message" name="message" required=""></textarea>
                                                          </div>
                      <button type = "submit" className="btn btn-secondary btn-lg">
                          Submit <i className="fas fa-arrow-right ml-1"></i>
                      </button>
                  </form>
              </div>
          </div>
      </div>
      <div className="col-lg-6">
          <div className="card bg-light border-0">
              <div className="card-body">
                  <div className="row">
                      <div className="col-md-6">
                          <p className="text-secondary"><i className="fas fa-headset fa-2x"></i></p>
                          <strong>Phone:</strong><br/>
                          <a className="text-body" href="tel:8192061175">8192061175</a>
                      </div>
                      <div className="col-md-6">
                          <p className="text-secondary"><i className="fas fa-envelope-open-text fa-2x"></i></p>
                          <strong>Email:</strong><br/>
                          <a className="text-body" href="mailto:contact@thesafecure.in">contact@thesafecure.in</a>
                      </div>
                  </div>
                  <hr/>
                  <p className="card-text text-secondary"><i className="fas fa-map-marked-alt fa-2x"></i></p>
                  <address>
                      <strong>24x7 The Safecure.</strong><br/>
                      Lady Lyall Campus<br/>Raja Ki Mandi Chauraha<br/>Agra - 282002
                  </address>
                  <hr/>
                  <p className="card-text text-right"><strong>Timings:</strong> 08:00 am - 12:00 am</p>
              </div>
          </div>
      </div>
  </div>
  </div>
      )  
  }  
}  
export default Contact  