import React from 'react'
import Form from './Form'
import Request from './Request'
import 'bootstrap/dist/css/bootstrap.css';

export default class FormSection extends React.Component{
    constructor(props){
        super(props);
        this.state={
            data:{
                heading: "All-in-one Healthcare",
                data : "Get expert medical care at home from a service provider trusted by many families for our passion towards caring",
                items : ["Qualified, mannered staff.","On-time fulfilment and instant replacement.","Prompt, satisfactory helpline."],
                buttonText: "Submit a requirement"   }
        }
    }
    render(){
        return (
            <section className="py-3 px-5 py-md-4 py-lg-5" style={{marginLeft : "2.5%",marginRight:"2.5%"}}>
            <div className = "row">
                <div className="col-lg-6 card ">
                    <Form/>
                </div>
                <div className="col-lg-6"  >
                    <Request  data = {this.state.data}/>
                    <Request  data = {this.state.data}/>
                </div>
            </div>
            </section>
        )
    }
}