import React from 'react';
export default class CallbackSection extends React.Component{
    constructor(props){
        super(props);
        this.state={
            name:"",
            mob_no:"",
            service:"Not Given"
        }
    }
    handleNameChange = (event)=>{
        var name = event.target.value
        this.setState(state=>({
            name:name,
            mob_no:state.mob_no,
            service:state.service

        }))
    }
    handleMobChange = (event)=>{
        var mob_no = event.target.value
        
        this.setState(state=>({
            name:state.name,
            mob_no:mob_no,
            service:state.service

        }))
    }
    handleSubmit = (event)=>{
        event.preventDefault()
        const url = 'https://0ikvfw3bp8.execute-api.ap-south-1.amazonaws.com/dev'
        
        const state = this.state
        const requestOptions = {
        method: 'POST',
        
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({name:state.name,mob_no:state.mob_no,service:state.service,type:'request'})
        };
        fetch(url, requestOptions)
        .then(response => alert("Thank You! We have recieved your request. We will be contacting you soon"))
        .catch(error => alert('OOops! The server Encounterd and error. You can call us for appointment'))
        this.setState({
            name:"",
            mob_no:"",
            service:"Not Given"
        })
    }
    render(){
        return(
            <section style={{marginLeft : "2.5%",marginRight:"2.5%"}}>
                <div className="py-3 py-md-4 py-lg-5">
                    <h3 className="text-center">
                        Get a Callback
                    </h3>
                    <p className="text-center mb-3 mb-lg-5">
                        Provide us with your name and phone number to get an instant callback from one of our healthcare consultants.
                    </p>

                    <center>
                    <form onSubmit={this.handleSubmit}>
                         <div className="row">
                            <div className="col-md-6 col-lg-4">
                                <div className="form-group mb-lg-0">
                                    
                                    <input onChange={this.handleNameChange} className="form-control form-control-lg " id="callback-name" required={true} name="name" placeholder="Name" value={this.state.name}/>
                                                </div>
                            </div>
                            <div className="col-md-6 col-lg-4">
                                <div className="form-group mb-lg-0">
                                    
                                    <input onChange = {this.handleMobChange} className="form-control form-control-lg " id="callback-phone" name="phone" placeholder="Phone number" required={true} type="tel" value={this.state.mob_no}/>
                                                </div>
                            </div>
                            <div className="col-md-6 col-lg-4">
                                <button type="submit" className="btn btn-primary btn-lg btn-block">
                                    <i className="fas fa-phone-alt mr-1"></i> Request Callback
                                </button>
                            </div>
                        </div>
                    </form>
                    </center>

                </div>
            </section>
        )
    }
}