import React from 'react';  
import ReactDOM from 'react-dom';  

import {
  HashRouter as Router,
  Route,
  Switch
  } from 'react-router-dom';
import Error from './Error';
import './index.css';  
import App from './App';  
import TandC from './TandC';
import PatientCare from './PatientCare';
import Privacy from './Privacy';
import Intro from './Intro';
import About from './about'  
import Contact from './contact'  
import CallbackSection from './CallbackSection';
import Navbar from './Navbar'
import Footer from './footer';
import HomeNursing from './HomeNursing';
import BabyCare from './BabyCare';
export default class Index extends React.Component{
  constructor(props){
    super(props);
    this.state = {
        curY : 0,
        isMobile : false,
        dropList : false,
        width : 1024
        
    }
  }
  toggleDropList= ()=>{
    //console.log(this.state.dropList);
    this.setState(state=>({
      curY : state.curY,
      isMobile:state.isMobile,
      dropList: !state.dropList,
      width:window.innerWidth
    }))

  }
  componentDidMount() {
    if (typeof window !== "undefined") {
      this.setState({curY:window.pageYOffset,isMobile:window.innerWidth<=770,width:window.innerWidth});
      

      window.onscroll = () => {
        let currentScrollPos = window.pageYOffset;
        // let maxScroll = document.body.scrollHeight - window.innerHeight;
        // //console.log(currentScrollPos)
        // //if (currentScrollPos > 45 && currentScrollPos < maxScroll) {
        //   
        this.setState({curY : currentScrollPos,isMobile : window.innerWidth<=770,width:window.innerWidth});
        
      }
      window.onresize = () =>{
        
        //console.log(window.innerWidth)
        this.setState({curY : window.pageYOffset,isMobile:window.innerWidth<=770,width:window.innerWidth})
      }

    }
  }
render(){
return (  
  <Router>  
    <div>  
      <h1>React Router Example</h1> 
      <Route path="/" render={()=><Navbar isMobile = {this.state.isMobile} curY = {this.state.curY} handleChange = {this.toggleDropList}/>} />  
      <Route exact path ="/" render = {()=><Intro dropList = {this.state.dropList} isMobile = {this.state.isMobile}/>} />
      <Route exact path="/" component={App} />
      
      <Switch>
        <Route exact path="/about" component={About} />  
        <Route exact path="/contact" render={()=><Contact width={this.state.width}/>} />
        <Route exact path ="/privacy-policy" component={Privacy}/>
        <Route exact path ="/services/patient-care" component = {PatientCare}/>
        <Route exact path ="/services/home-nursing" component = {HomeNursing}/>
        <Route exact path ="/terms-and-conditions" component={TandC}/>
        <Route exact path ="/services/new-born-baby-care" component={BabyCare}/>
        <Route component={Error}></Route> // Error Page
      </Switch>

      <Route path="/" component={CallbackSection}/>
      <Route path="/" component={Footer} />  
      
    </div>  
  </Router>  
)  
}}
ReactDOM.render(<Index/>, document.getElementById('root'));  