import React from 'react';
import Service from './Service'
export default class ServicesSection extends React.Component{
    
    constructor(props){
        super(props);
        this.state = {
            services : [
                {
                    serviceName : "Home Nursing",
                    serviceDescription : "Get qualified, skilled nurses for at home, expert care.",
                    serviceImageUrl : "https://image.flaticon.com/icons/png/128/1165/1165491.png"//Give Credits for this image or change image
                },
                
                {
                    serviceName : "Elderly Care",
                    serviceDescription : "Helping hand for your parents when you not around.",
                    serviceImageUrl : "https://cdn.imgbin.com/22/1/21/imgbin-computer-icons-symbol-symbol-iJeMSYQMXwmpgC3miFRM9cPZ3.jpg"//Give Credits for this image or change image
                },
                {
                    serviceName : "New Born Baby Care",
                    serviceDescription : "Caring nanny for new born babies just a call away.",
                    serviceImageUrl : "https://image.flaticon.com/icons/png/128/1165/1165491.png"//Give Credits for this image or change image
                }
                ,{
                    serviceName : "Patient Care",
                    serviceDescription : "Get qualified, skilled nurses for at home, expert care.",
                    serviceImageUrl : "https://image.flaticon.com/icons/png/128/1165/1165491.png"//Give Credits for this image or change image
                }
            ]
        }
    }
    render(){
        return(
            <section className="bg-light py-3 py-md-4 py-lg-5" style={{marginLeft : "2.5%",marginRight:"2.5%"}}>
                <div className="container">
                    <h3 className="text-center">
                        Your Healthcare Partner
                    </h3>
                    <p className="text-center mb-3 mb-lg-5">
                    Swasthya stays around you in all the situations, making your world happier and healthier with our quality healthcare at home service catalog.
                    </p>
                    
                    <div className="row">
                       <Service data = {this.state.services[0]}/>
                       <Service data = {this.state.services[1]}/>
                       <Service data = {this.state.services[2]}/>
                       <Service data = {this.state.services[3]}/>
                       
                    </div>
                </div>
            </section>
        )
    }
}