import React from 'react';
import './from.css';

export default class Request extends React.Component{
    constructor(props){
        super(props);
    }
    
    render(){
        const data = this.props.data;
            
        return(
            <div className=" req card bg-dark text-white mb-2" >
                <div className="card-body">
                    <h3 className="card-title">
                    {data.heading}
                    </h3>
                    
                    <p className="card-text">{data.intro}</p>
                    <ul >{
                            data.items.map( (item) => {
                                return (<li key={item}><i className="fas fa-file-prescription"></i> {item}</li>)

                            })
                        }
                    </ul>
                    <button className="btn btn-primary">
                        {data.buttonText}
                    </button>

                </div>
            </div>
                
        )
    }
}