import React from 'react';
export default class Privacy extends React.Component{
    render(){
        return(
            <div  style={{marginTop:"4em"}}>
            <section className="text-white py-3 py-md-4 py-lg-5" id="section-header" style={{backgroundColor:'black',width:'100%'}}>
        <div className="container">
            <h1 className="text-center mb-3">Privacy Policy</h1>
            <div className="row justify-content-center">
                <div className="col-md-10 col-lg-9 col-xl-8">
                    <p className="lead text-center mb-0">
                        Your data is protected with us under below document.
                        Your usage of this website and our products/services implies your agreement to these policies.
                    </p>
                </div>
            </div>
        </div>
    </section>
    <section className="py-3 py-md-4 py-lg-5">
        <div className="container">
            <p><strong>PRIVACY NOTICE</strong></p>
            <p><strong>Last updated June 01, 2020</strong></p>
            <p>Thank you for choosing to be part of our community at The Safecure, doing business as The Safecure (“<strong>The Safecure</strong>”, “<strong>we</strong>”, “<strong>us</strong>”, or “<strong>our</strong>”). We are committed to protecting your personal information and your right to privacy. If you have any questions or concerns about this privacy notice, or our practices with regards to your personal information, please contact us at contact@thsafecure.in.</p>
            <p>When you visit our website <a href="https:/thesafecure.in/">https://thesafecure.in/</a> (the "<strong>Website</strong>"), and more generally, use any of our services (the "<strong>Services</strong>", which include the Website), we appreciate that you are trusting us with your personal information. We take your privacy very seriously. In this privacy notice, we seek to explain to you in the clearest way possible what information we collect, how we use it and what rights you have in relation to it. We hope you take some time to read through it carefully, as it is important. If there are any terms in this privacy notice&nbsp;that you do not agree with, please discontinue use of our Services immediately.</p>
            <p>This privacy notice applies to all information collected through our Services (which, as described above, includes our Website), as well as any related services, sales, marketing or events.</p>
            <p><strong>Please read this privacy notice&nbsp;carefully as it will help you understand what we do with the information that we collect.</strong></p>
            <p><strong>TABLE OF CONTENTS</strong></p>
            
            <ol>
                <li><strong> WHAT INFORMATION DO WE COLLECT?</strong></li>
            </ol>
            <p><strong><br/>Personal information you disclose to us</strong></p>
            <p><strong><em>In Short: </em></strong><em>We collect information that you provide to us.</em></p>
            <p>We collect personal information that you voluntarily provide to us when you register on the Website, express an interest in obtaining information about us or our products and Services, when you participate in activities on the Website (such as by posting messages in our online forums or entering competitions, contests or giveaways) or otherwise when you contact us.</p>
            <p>The personal information that we collect depends on the context of your interactions with us and the Website, the choices you make and the products and features you use. The personal information we collect may include the following:</p>
            <p><strong>Personal Information Provided by You.</strong> We collect names; phone numbers; email addresses; mailing addresses; passwords; contact preferences; contact or authentication data; billing addresses; and other similar information.</p>
            <p><strong>Payment Data.&nbsp;</strong>We may collect data necessary to process your payment if you make purchases, such as your payment instrument number (such as a credit card number), and the security code associated with your payment instrument. All payment data is stored by Razorpay. You may find their privacy notice link(s) here: <a href="https://razorpay.com/privacy/">https://razorpay.com/privacy/</a>.</p>
            <p>All personal information that you provide to us must be true, complete and accurate, and you must notify us of any changes to such personal information.</p>
            <p><strong><br/>Information automatically collected</strong></p>
            <p><em><strong>In Short: </strong>Some information — such as your Internet Protocol (IP) address and/or browser and device characteristics — is collected automatically when you visit our Website.</em></p>
            <p>We automatically collect certain information when you visit, use or navigate the Website. This information does not reveal your specific identity (like your name or contact information) but may include device and usage information, such as your IP address, browser and device characteristics, operating system, language preferences, referring URLs, device name, country, location, information about who and when you use our Website and other technical information. This information is primarily needed to maintain the security and operation of our Website, and for our internal analytics and reporting purposes.</p>
            <p>Like many businesses, we also collect information through cookies and similar technologies. &nbsp;</p>
            <p>The information we collect includes:</p>
            <ul>
                <li><em>Log and Usage Data.</em> Log and usage data is service-related, diagnostic usage and performance information our servers automatically collect when you access or use our Website and which we record in log files. Depending on how you interact with us, this log data may include your IP address, device information, browser type and settings and information about your activity in the Website (such as the date/time stamps associated with your usage, pages and files viewed, searches and other actions you take such as which features you use), device event information (such as system activity, error reports (sometimes called 'crash dumps') and hardware settings).</li>
            </ul>
            <ul>
                <li><em>Device Data.&nbsp;</em>We collect device data such as information about your computer, phone, tablet or other device you use to access the&nbsp;Website. Depending on the device used, this device data may include information such as your IP address (or proxy server), device application identification numbers, location, browser type, hardware model Internet service provider and/or mobile carrier, operating system configuration&nbsp;information.</li>
            </ul>
            <ul>
                <li><em>Location Data.</em> We collect information data such as information about your device's location, which can be either precise or imprecise. How much information we collect depends on the type of settings of the device you use to access the Website. For example, we may use GPS and other technologies to collect geolocation data that tells us your current location (based on your IP address). You can opt out of allowing us to collect this information either by refusing access to the information or by disabling your Locations settings on your device. Note however, if you choose to opt out, you may not be able to use certain aspects of the Services.</li>
            </ul>
            <ol start="2">
                <li><strong> HOW DO WE USE YOUR INFORMATION?</strong></li>
            </ol>
            <p><em><strong>In Short: </strong>We process your information for purposes based on legitimate business interests, the fulfillment of our contract with you, compliance with our legal obligations, and/or your consent.</em></p>
            <p>We use personal information collected via our Website for a variety of business purposes described below. We process your personal information for these purposes in reliance on our legitimate business interests, in order to enter into or perform a contract with you, with your consent, and/or for compliance with our legal obligations. We indicate the specific processing grounds we rely on next to each purpose listed below.</p>
            <p>We use the information we collect or receive:</p>
            <ul>
                <li><strong>To facilitate account creation and logon process.</strong>&nbsp;If you choose to link your account with us to a third-party account (such as your Google or Facebook account), we use the information you allowed us to collect from those third parties to facilitate account creation and logon process for the performance of the contract. <br/><br/></li>
                <li><strong>To post testimonials.</strong>&nbsp;We post testimonials on our Website that may contain personal information. Prior to posting a testimonial, we will obtain your consent to use your name and the consent of the testimonial. If you wish to update, or delete your testimonial, please contact us at contact@thesafecure.in and be sure to include your name, testimonial location, and contact information.<br/><br/></li>
                <li><strong>Request feedback.</strong>&nbsp;We may use your information to request feedback and to contact you about your use of our Website.<br/><br/></li>
                <li><strong>To enable user-to-user communications.</strong>&nbsp;We may use your information in order to enable user-to-user communications with each user's consent.<br/><br/></li>
                <li><strong>To manage user accounts</strong>. We may use your information for the purposes of managing our account and keeping it in working order.</li>
            </ul>
            <ul>
                <li><strong>To send administrative information to you.&nbsp;</strong>We may use your personal information to send you product, service and new feature information and/or information about changes to our terms, conditions, and policies.<br/><br/></li>
                <li><strong>To protect our Services.</strong>&nbsp;We may use your information as part of our efforts to keep our Website safe and secure (for example, for fraud monitoring and prevention).<br/><br/></li>
                <li><strong>To enforce our terms, conditions and policies for business purposes, to comply with legal and regulatory requirements or in connection with our contract.</strong><br/><br/></li>
                <li><strong>To respond to legal requests and prevent harm.&nbsp;</strong>If we receive a subpoena or other legal request, we may need to inspect the data we hold to determine how to respond.</li>
            </ul>
            <ul>
                <li><strong>Fulfill and manage your orders.</strong>&nbsp;We may use your information to fulfill and manage your orders, payments, returns, and exchanges made through the Website.<br/><br/></li>
                <li><strong>Administer prize draws and competitions.&nbsp;</strong>We may use your information to administer prize draws and competitions when you elect to participate in our competitions.<br/><br/></li>
                <li><strong>To deliver and facilitate delivery of services to the user.</strong> We may use your information to provide you with the requested service.<br/><br/></li>
                <li><strong>To respond to user inquiries/offer support to users.</strong> We may use your information to respond to your inquiries and solve any potential issues you might have with the use of our Services.</li>
            </ul>
            <ul>
                <li><strong>To send you marketing and promotional communications.</strong>&nbsp;We and/or our third-party marketing partners may use the personal information you send to us for our marketing purposes, if this is in accordance with your marketing preferences. For example, when expressing an interest in obtaining information about us or our Website, subscribing to marketing or otherwise contacting us, we will collect personal information from you. You can opt-out of our marketing emails at any time (see the "<a href="#privacyrights">WHAT ARE YOUR PRIVACY RIGHTS</a>" below).<br/><br/></li>
                <li><strong>Deliver targeted advertising to you.&nbsp;</strong>We may use your information to develop and display personalized content and advertising (and work with third parties who do so) tailored to your interests and/or location and to measure its effectiveness.</li>
            </ul>
            <ol start="3">
                <li><strong> WILL YOUR INFORMATION BE SHARED WITH ANYONE?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>We only share information with your consent, to comply with laws, to provide you with services, to protect your rights, or to fulfill business obligations.</em></p>
            <p>We may process or share your data that we hold based on the following legal basis:</p>
            <ul>
                <li><strong>Consent:</strong> We may process your data if you have given us specific consent to use your personal information in a specific purpose.<br/><br/></li>
                <li><strong>Legitimate Interests:</strong> We may process your data when it is reasonably necessary to achieve our legitimate business interests.<br/><br/></li>
                <li><strong>Performance of a Contract:&nbsp;</strong>Where we have entered into a contract with you, we may process your personal information to fulfill the terms of our contract.<br/><br/></li>
                <li><strong>Legal Obligations:</strong> We may disclose your information where we are legally required to do so in order to comply with applicable law, governmental requests, a judicial proceeding, court order, or legal process, such as in response to a court order or a subpoena (including in response to public authorities to meet national security or law enforcement requirements).<br/><br/></li>
                <li><strong>Vital Interests:</strong> We may disclose your information where we believe it is necessary to investigate, prevent, or take action regarding potential violations of our policies, suspected fraud, situations involving potential threats to the safety of any person and illegal activities, or as evidence in litigation in which we are involved.</li>
            </ul>
            <p>More specifically, we may need to process your data or share your personal information in the following situations:</p>
            <ul>
                <li><strong>Business Transfers.</strong>&nbsp;We may share or transfer your information in connection with, or during negotiations of, any merger, sale of company assets, financing, or acquisition of all or a portion of our business to another company.</li>
            </ul>
            <ul>
                <li><strong>Other Users.</strong>&nbsp;When you share personal information or otherwise interact with public areas of the Website, such personal information may be viewed by all users and may be publicly made available outside the Website in perpetuity. Similarly, other users will be able to view descriptions of your activity, communicate with you within our Website, and view your profile.</li>
            </ul>
            <ol start="4">
                <li><strong> DO WE USE COOKIES AND OTHER TRACKING TECHNOLOGIES?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>We may use cookies and other tracking technologies to collect and store your information.</em></p>
            <p>We may use cookies and similar tracking technologies (like web beacons and pixels) to access or store information. Specific information about how we use such technologies and how you can refuse certain cookies is set out in our Cookie Notice.</p>
            <ol start="5">
                <li><strong> DO WE USE GOOGLE MAPS?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>Yes, we use Google Maps for the purpose of providing better service.</em></p>
            <p>This Website uses Google Maps APIs which is subject to Google's Terms of Service. You may find the Google Maps APIs Terms of Service&nbsp;<a href="https://developers.google.com/maps/terms">here</a>. To find out more about Google’s Privacy Policy, please refer to this&nbsp;<a href="https://policies.google.com/privacy">link</a>. We obtain and store on your device ('cache') your location. You may revoke your consent anytime by contacting us at the contact details provided at the end of this document. </p>
            <ol start="6">
                <li><strong> HOW LONG DO WE KEEP YOUR INFORMATION?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>We keep your information for as long as necessary to fulfill the purposes outlined in this privacy notice&nbsp;unless otherwise required by law.</em></p>
            <p>We will only keep your personal information for as long as it is necessary for the purposes set out in this privacy notice, unless a longer retention period is required or permitted by law (such as tax, accounting or other legal requirements). No purpose in this notice will require us keeping your personal information for longer than twelve (12) months past the start of idle period of the user's account.</p>
            <p>When we have no ongoing legitimate business need to process your personal information, we will either delete or anonymize such information, or, if this is not possible (for example, because your personal information has been stored in backup archives), then we will securely store your personal information and isolate it from any further processing until deletion is possible.</p>
            <ol start="7">
                <li><strong> HOW DO WE KEEP YOUR INFORMATION SAFE?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>We aim to protect your personal information through a system of organizational and technical security measures.</em></p>
            <p>We have implemented appropriate technical and organizational security measures designed to protect the security of any personal information we process. However, despite our safeguards and efforts to secure your information, no electronic transmission over the Internet or information storage technology can be guaranteed to be 100% secure, so we cannot promise or guarantee that hackers, cybercriminals, or other unauthorized third parties will not be able to defeat our security, and improperly collect, access, steal, or modify your information. Although we will do our best to protect your personal information, transmission of personal information to and from our Website is at your own risk. You should only access the Website within a secure environment.</p>
            <ol start="8">
                <li><strong> DO WE COLLECT INFORMATION FROM MINORS?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>We do not knowingly collect data from or market to children under 18 years of age.</em></p>
            <p>We do not knowingly solicit data from or market to children under 18 years of age. By using the Website, you represent that you are at least 18 or that you are the parent or guardian of such a minor and consent to such minor dependent’s use of the Website. If we learn that personal information from users less than 18 years of age has been collected, we will deactivate the account and take reasonable measures to promptly delete such data from our records. If you become aware of any data we may have collected from children under age 18, please contact us at contact@thsafecure.in.</p>
            <ol start="9">
                <li><strong> WHAT ARE YOUR PRIVACY RIGHTS?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>You may review, change, or terminate your account at any time.</em></p>
            <p>If you are resident in the European Economic Area and you believe we are unlawfully processing your personal information, you also have the right to complain to your local data protection supervisory authority. You can find their contact details here:&nbsp;<a href="http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm">http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm</a>.</p>
            <p>If you are resident in Switzerland, the contact details for the data protection authorities are available here: <a href="https://www.edoeb.admin.ch/edoeb/en/home.html">https://www.edoeb.admin.ch/edoeb/en/home.html</a>.</p>
            <p>If you have questions or comments about your privacy rights, you may email us at contact@thsafecure.in.</p>
            <p><strong>Account Information</strong></p>
            <p>If you would at any time like to review or change the information in your account or terminate your account, you can:</p>
            <p> ■ Log in to your account settings and update your user account. </p>
            <p> ■ Contact us using the contact information provided. </p>
            <p>Upon your request to terminate your account, we will deactivate or delete your account and information from our active databases. However, we may retain some information in our files to prevent fraud, troubleshoot problems, assist with any investigations, enforce our Terms of Use and/or comply with applicable legal requirements.</p>
            <p><strong><u>Cookies and similar technologies:</u>&nbsp;</strong>Most Web browsers are set to accept cookies by default. If you prefer, you can usually choose to set your browser to remove cookies and to reject cookies. If you choose to remove cookies or reject cookies, this could affect certain features or services of our Website. To opt-out of interest-based advertising by advertisers on our Website&nbsp;visit&nbsp;<a href="http://www.aboutads.info/choices/">http://www.aboutads.info/choices/</a>. </p>
            <p><strong><u>Opting out of email marketing:</u>&nbsp;</strong>You can unsubscribe from our marketing email list at any time by clicking on the unsubscribe link in the emails that we send or by contacting us using the details provided below. You will then be removed from the marketing email list – however, we may still communicate with you, for example to send you service-related emails that are necessary for the administration and use of your account, to respond to service requests, or for other non-marketing purposes. To otherwise opt-out, you may:</p>
            <p> ■ Contact us using the contact information provided. </p>
            <ol start="10">
                <li><strong> CONTROLS FOR DO-NOT-TRACK FEATURES</strong></li>
            </ol>
            <p>Most web browsers and some mobile operating systems and mobile applications include a Do-Not-Track (“DNT”) feature or setting you can activate to signal your privacy preference not to have data about your online browsing activities monitored and collected. At this stage, no uniform technology standard for recognizing and implementing DNT signals has been finalized. As such, we do not currently respond to DNT browser signals or any other mechanism that automatically communicates your choice not to be tracked online. If a standard for online tracking is adopted that we must follow in the future, we will inform you about that practice in a revised version of this privacy notice.</p>
            <ol start="11">
                <li><strong> DO WE MAKE UPDATES TO THIS NOTICE?</strong></li>
            </ol>
            <p><strong><em>In Short:&nbsp;</em>&nbsp;</strong><em>Yes, we will update this notice as necessary to stay compliant with relevant laws.</em></p>
            <p>We may update this privacy notice&nbsp;from time to time. The updated version will be indicated by an updated “Revised” date and the updated version will be effective as soon as it is accessible. If we make material changes to this privacy notice, we may notify you either by prominently posting a notice of such changes or by directly sending you a notification. We encourage you to review this privacy notice&nbsp;frequently to be informed of how we are protecting your information.</p>
            <ol start="12">
                <li><strong> HOW CAN YOU CONTACT US ABOUT THIS NOTICE?</strong></li>
            </ol>
            <p>If you have questions or comments about this notice, you may email us at contact@thsafecure.in or by post to:</p>
            <p>Lady Lyall Hospital Campus&nbsp;</p>
            <p>Raja Ki Mandi Chauraha</p>
            <p>Agra, 282002</p>
            <p>India</p>
        </div>
    </section>  
         </div>
        )
    }
}