import React from 'react';
import nurses from './nurses.jpg';
import './intro.css'
import { Link } from 'react-router-dom';
export default class Intro extends React.Component{
    constructor(props){
        super(props);
    }
    scrollToTop=()=>{
        window.scrollTo(0,0)
    }
    render(){
        return(
            <section className={ this.props.isMobile?(this.props.dropList?" dropList ":" noDropList "):""} id="section-banner" >
        <div className="container">
            <div className="row">
                <div className="col-lg-6 col-xl-7 d-flex">
                    <div className="w-100 my-auto">
                        <h1 className="font-alt text-center text-lg-left display-4 mb-3 mb-lg-5">
                            <span className="bg-primary text-white"><span className="d-none d-sm-inline">Exceptional</span> Care</span>
                            <br/>
                            <span className="bg-dark text-white">Close To You</span>
                        </h1>
                        <div className="mystyle text-white rounded-lg p-4 mb-3 mb-lg-5">
                            <h4><u>24x7 Home Healthcare Services</u></h4>
                            <p className="lead">
                                We provide personalized nursing care as per patient's needs and requirements,
                                while being more compassionate towards the patient.
                            </p>
                            
                            </div>
                        <div className="btn-toolbar">
                            <a className="intro-btn btn btn-light btn-lg" href="tel:8192061175" type="button">
                                <i className="fas fa-phone-alt mr-1"></i> Call Us
                            </a>
                            
                            <Link className="intro-btn btn btn-dark btn-lg ml-2" to="/contact" type="button" onClick={this.scrollToTop}>
                                Enquire <i className="fas fa-arrow-right ml-1"></i>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
        )
    }
}